.todoList{min-height:250px}.todoList .todoList__item{padding:10px 5px}.todoList .todoList__item span{color:#4d4d4d;font-size:18px}.todoList .todoList__item .task-completed{text-decoration:line-through;color:#c9c7c7}.todoList .todoList__item:not(:last-child){border-bottom:1px solid #f1f1f1}label{display:flex;align-items:center}.checkbox-wrapper input[type=checkbox]{-webkit-appearance:none;appearance:none;width:1.6em;height:1.6em;border-radius:.15em;margin-right:1em;border:.15em solid #007a7e;width:30px;height:30px;border-radius:100%;outline:none;cursor:pointer}input.checked{position:relative}input.checked::before{content:"âœ”";font-size:1.5em;color:#007a7e;position:absolute;right:4px;top:-2px}.checkbox-wrapper input[type=checkbox]:disabled{border-color:silver;background-color:silver}.checkbox-wrapper input[type=checkbox]:disabled+span{color:silver}.checkbox-wrapper input[type=checkbox]:focus{box-shadow:0 0 20px #007a7e}